form{
    display: flex;
    justify-content: center;
    align-items: center;
}
.wrap_input input{
    border: none;
    outline: none;
    display: flex;
    justify-content: center;
    width: 100%;
    background:linear-gradient(35deg, #494949, #313131);
    color: #ececec;
    padding: 12px 40px;
    font-size: 18px;
    border-radius: 10px;
}
.wrap_input{
    position: relative;
    width: 60%;
    background: linear-gradient(35deg, #494949, #313131);
    border-radius: 10px;
    margin: 15px 0;

}
.wrap_input  svg{
    position: absolute;
    top: 50%;
    left: 0;
    transform: translate(100%, -50%);
    color: #ececec;
}
@media screen and (max-width: 768px){
    .wrap_input{
        width: 75%;
    }
}
@media screen and (max-width: 480px){
    .wrap_input{
        width: 100%;
    }
}